	module.exports = {"data":{"wordpressPost":{"title":"Top 10 most read in August: Caesarean sections and private medical insurance, seeking ambulance treatment for primary care problems, and social network interventions in type 2 diabetes","content":"<p>August sees eight new entries in the top 10 most read articles. Maintaining the top position for the second consecutive month is longitudinal cohort study by Xie <em>et al, </em>which examines the association between Proton Pump Inhibitors and the risk of all-cause mortality.</p>\n<p><a href=\"https://blogs.bmj.com/bmjopen/files/2017/09/baby-1178539_1920.jpg\"><img class=\"size-medium wp-image-1143 alignright\" src=\"https://blogs.bmj.com/bmjopen/files/2017/09/baby-1178539_1920-300x200.jpg\" alt=\"\" width=\"300\" height=\"200\" srcset=\"https://blogs.bmj.com/bmjopen/files/2017/09/baby-1178539_1920-300x200.jpg 300w, https://blogs.bmj.com/bmjopen/files/2017/09/baby-1178539_1920-768x511.jpg 768w, https://blogs.bmj.com/bmjopen/files/2017/09/baby-1178539_1920-1024x681.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /></a></p>\n<p>At number two is a new entry from Vreman <em>et al</em>, who use a microsimulation model to assess the health and economic benefits of reducing sugar intake in the USA. This is followed by an ecological analysis by Diniz <em>et al, </em>looking at the factors associated with breast cancer mortality in São Paulo, Brazil between 2006 and 2012.</p>\n<p>Booker <em>at al</em> reach number four this month with a qualitative systematic review that investigates the motivation for and experience of seeking emergency ambulance treatment for primary care conditions. The findings suggest that complex socioemotional factors and physical symptoms are involved in the decision making process, which can be influenced by caregivers.</p>\n<p>At number six is a systematic review and meta-analysis exploring the association between private medical insurance and the odds of caesarean section. The authors found that caesarean sections were more likely to be performed in women who were privately insured, compared to those who were not. The authors call for a review of payment and reimbursement schemes to determine influencing factors.</p>\n<p>Finally, at number 10 this month is a systematic review and meta-analysis from Spencer-Bonilla <em>et al.</em> The authors sought to establish the effect of social network interventions on health outcomes in patients with type 2 diabetes. The authors conclude that while the existing body of evidence is limited, the findings are encouraging.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Xie <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015735\">Risk of death among users of Proton Pump Inhibitors: a longitudinal observational cohort study of United States veterans</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Vreman<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/8/e013543\">Health and economic benefits of reducing sugar intake in the USA, including effects via non-alcoholic fatty liver disease: a microsimulation model</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Diniz<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\">\n<div class=\"highwire-cite-title\"><a href=\"http://bmjopen.bmj.com/content/7/8/e016395\">Breast cancer mortality and associated factors in Sao Paulo State, Brazil: an ecological analysis</a></div>\n</td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Booker <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/8/e016832\">Seeking ambulance treatment for &#8216;primary care problems: a qualitative systematic review of patient, carer and professional perspectives</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Manduca <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e014035\">A cross sectional study of the relationship between the exposure of pregnant women to military attacks in 2014 in Gaza and the load of heavy metal contaminants in the hair of mothers and newborns</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Hoxha <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/8/e016600\">Caesarean sections and private insurance: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Nickel et<em> al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e014129.long\">Words do matter: a systematic review on how different terminology for the same condition influences management preferences</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Gadie<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e014920\">How are age-related differences in sleep quality associated with health outcomes? An epidemiological investigation in a UK cohort of 2406 adults</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Ferrando<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e016765.long\">Rationale and study design for an individualised perioperative open-lung ventilatory strategy with a high versus conventional inspiratory oxygen fraction (iPROVE-O2) and its effects on surgical site infection: study protocol for a randomised controlled triCarlos</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Spencer-Bonilla <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/8/e016506\">A systematic review and meta-analysis of trials of social network interventions in type 2 diabetes</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"421a9523-b972-5d43-85b0-c77b0e369be7"}}

